import React, { Component } from 'react'
import Axios from 'axios'
import '../App.css'

class Body extends Component {
    constructor(props) {
        super(props);
        this.state = {
            emailid : "",
            isValid: false
        };
    }

    handleChange = (e) => {
        if(this.validateEmail(e.target.value)){
            this.setState({
                isValid: true
            })
        }
        else{
            this.setState({
                isValid: false
            })
        }
    }

    handleClick = (e) => {
        e.preventDefault();
        this.setState({
            emailid: document.getElementById('emailid').value
            }, () => {
                this.handleInsert();
        });   
        
    }

    validateEmail(email){
        var pattern1 = new RegExp(/^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i);
        if (!pattern1.test(email)) {
            return false;
        }

        var pattern2 = new RegExp(/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/);
        if (!pattern2.test(email)) {
            return false;
        }
        
        return true;
    }

    handleInsert() {
        Axios.get("https://newsletter-backend-api.herokuapp.com/api/insert?emailid="+this.state.emailid)
        .then(response => {
            if(response.data[0]["res"] == 0)
            {
                alert('Thanks for your Love!\nYou have already subscribed!!!');
            }
            else if(response.data[0]["res"] == 1)
            {
                alert('Subscription Successful!');
            }
            
        }).catch((err) => {
            console.log(err);
        })
    }

    render() {
        return (
            <div className="main">
                <div className="p">
                    <div className="txt">
                        <div style={{fontSize:"20px"}}><i><b>Preparing for interviews?</b></i><br/></div>
                        Subscribe to this newsletter and
                        get daily practice problems to get <b>One Step Closer</b>
                    </div>
                    <input id="emailid" type="email" name="name" style={{width: '25%'}} 
                    onChange={this.handleChange} autoComplete='off' placeholder="Enter your Email Id..." />
                    <br/>
                    <button style={{width: '25%', backgroundColor: 'lightblue'}} onClick={this.handleClick} disabled={!this.state.isValid}>Subscribe Now!</button>
                    <div style={{ color: 'red', display:this.state.isValid ? 'none': 'block'}}>Please enter a valid Email Address</div>
                </div>
                <div className="content">
                    <div style={{fontSize:"20px"}}><b>Best way to use?</b></div>
                    <ul style={{listStyleType: 'none'}}>
                        <li>
                            Every mail is divided into 2 sections <i>Theory</i> and <i>Practice</i>
                        </li>
                        <li>
                            Go through the <i>Theory</i> first and spend some time understanding the concepts
                        </li>
                        <li>
                            Once you are thorough with the theory part you can move to the <i>Practice</i> section and solve them
                        </li>
                    </ul>
                </div>
            </div>
        )
    }
}

export default Body;