import React, { Component } from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import '../App.css'

class Navbar extends Component {
    constructor(props) {
        super(props);
        this.state = {
            userscount : 0
        };
    }

    componentDidMount() {
        this.getUsersCount();
    }

    getUsersCount = () => {
        fetch("https://newsletter-backend-api.herokuapp.com/api/userscount")
            .then(response => response.json())
            .then(data => {
                this.setState({
                    userscount: data[0]['cnt']
                })
            });
    }

    render() {
        return (
            <nav className="navbar navbar-expand-lg navbar-light bg-dark">
                <div className="mx-auto">
                    <span className="navbar-brand h1 text-white" style={{fontSize:'50 px'}}>
                        Number of Active Users: {this.state.userscount}
                    </span>
                </div>
            </nav>
        )
    }
}

export default Navbar;